.profile__title {
    font-weight: 500;
    font-size: 42px;
    line-height: 1.3;
    margin: 0;
    margin-right: 17px;
    white-space: nowrap; 
    overflow: hidden; 
    text-overflow: ellipsis; 
}

@media screen and (max-width: 720px) {
    .profile__title {
        font-size: 27px;
        line-height: 2;
        max-width: 90%;
    }
}